.nav {
  width: 100%;
  height: 80px;
  background: #fff;
  border-bottom: 1px #f2f2f2 solid;
  display: flex;
  justify-content: left;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 1; }
  .nav .searchBar {
    height: 40px;
    background: #fff;
    box-shadow: 0 0 2pt 1pt #cccccc73;
    display: inline-block; }
    .nav .searchBar svg {
      padding-left: 10px; }
    .nav .searchBar input {
      height: 100%;
      border: none;
      padding: 0;
      outline: none;
      padding-left: 10px; }
  .nav .right {
    position: absolute;
    right: 0;
    margin-right: 40px; }
  .nav .left {
    margin-left: 40px; }
    .nav .left .video_icon {
      font-size: 20px;
      font-size: 25px;
      color: #ff0000;
      margin-right: 20px; }
    .nav .left a {
      margin-left: 20px;
      outline: none;
      text-decoration: none;
      color: #000; }
